@charset "utf-8";

/* 초기화 */
html {
  overflow-y: scroll;
  height: 100%;
  min-width: 320px;
}
body {
  margin: 0;
  padding: 0;
  font-size: 0.75em;
  background: #f7f8f9;
  height: 100%;
  font-family: "Apple SD Gothic Neo", "Malgun Gothic", "맑은 고딕", sans-serif;
}
body,
h1,
h2,
h3,
h4,
h5,
h6,
input,
button,
textarea,
select {
  font-family: "Apple SD Gothic Neo", "Malgun Gothic", "맑은 고딕", sans-serif;
}
html,
h1,
h2,
h3,
h4,
h5,
h6,
form,
fieldset,
img {
  margin: 0;
  padding: 0;
  border: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1em;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
legend {
  position: absolute;
  font-size: 0;
  line-height: 0;
  text-indent: -9999em;
  overflow: hidden;
}
label,
input,
button,
select,
img {
  font-family: "Apple SD Gothic Neo", "Malgun Gothic", "맑은 고딕", sans-serif;
  vertical-align: middle;
}
input,
button {
  margin: 0;
  padding: 0;
  font-size: 1em;
}
button {
  cursor: pointer;
  font-family: "Apple SD Gothic Neo", "Malgun Gothic", "맑은 고딕", sans-serif;
}
input[type="text"],
input[type="password"],
input[type="submit"],
input[type="image"],
button {
  font-size: 1em;
  -webkit-appearance: none;
}
textarea,
select {
  font-size: 1em;
  font-family: "Apple SD Gothic Neo", "Malgun Gothic", "맑은 고딕", sans-serif;
}
textarea {
  border-radius: 0;
  -webkit-appearance: none;
  border: 1px solid #d9dee9;
}
select {
  margin: 0;
  background: none;
}
p {
  margin: 0;
  padding: 0;
  word-break: break-all;
}
hr {
  display: none;
}
pre {
  overflow-x: scroll;
  font-size: 1.1em;
}
a {
  color: #000;
  text-decoration: none;
}
ul,
li,
dl,
dt,
dd {
  list-style: none;
  padding: 0;
  margin: 0;
}
*,
:after,
:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

/* 팝업레이어 */
#hd_pop {
  z-index: 9;
  position: relative;
  margin: 0 auto;
  width: 100%;
  height: 0;
}
#hd_pop h2 {
  position: absolute;
  font-size: 0;
  text-indent: -9999em;
  line-height: 0;
  overflow: hidden;
}
.hd_pops {
  position: absolute;
  border: 1px solid #e9e9e9;
  background: #fff;
}
.hd_pops img {
  max-width: 100%;
  height: auto;
}
.hd_pops_con {
}
.hd_pops_footer {
  padding: 10px 0;
  background: #000;
  color: #fff;
  text-align: right;
}
.hd_pops_footer button {
  margin-right: 5px;
  padding: 5px 10px;
  border: 0;
  background: #393939;
  color: #fff;
}

/* 상단 레이아웃 */
#header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
  transition: left 0.2s;
}
#header:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
#hd_h1 {
  position: absolute;
  font-size: 0;
  text-indent: -9999em;
  line-height: 0;
  overflow: hidden;
}

/* 본문 바로가기 */
.to_content a {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  font-size: 0;
  line-height: 0;
  overflow: hidden;
}
#mobile-indicator {
  display: none;
}

#hd_wrapper {
  position: relative;
  margin: 0 auto;
  padding: 10px;
  height: 61px;
  background: #282c37;
}
#hd_wrapper:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
#hd_wrapper .gnb_side_btn {
  float: left;
  display: inline-block;
  margin-top: 6px;
}
#hd_wrapper .gnb_side_btn a {
  display: none;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 2em;
  vertical-align: top;
  cursor: pointer;
  color: #fff;
}

#logo {
  float: left;
  display: inline-block;
  margin: 5px 10px 0;
}

.header_ct {
  float: right;
}
.hd_sch_wr {
  display: inline-block;
  float: right;
  margin-left: 10px;
}
.hd_sch_bt {
  position: relative;
  width: 30px;
  height: 30px;
  margin-top: 5px;
  margin-left: 5px;
  vertical-align: top;
  font-size: 1.5em;
  cursor: pointer;
  color: #fff;
  background: transparent;
  border: 0;
}

#hd_sch {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 1;
  background: red;
}
#hd_sch > h2 {
  position: absolute;
  font-size: 0;
  text-indent: -9999em;
  line-height: 0;
  overflow: hidden;
}
#hd_sch form {
  position: relative;
}
#hd_sch #sch_stx {
  width: 100%;
  height: 60px;
  padding-left: 10px;
  border: 0;
  background: #282c37;
  color: #fff;
  z-index: 1;
}
#hd_sch #sch_submit {
  position: absolute;
  top: 0;
  right: 75px;
  width: 60px;
  height: 60px;
  border: 0;
  cursor: pointer;
  color: #fff;
  background: transparent;
  font-size: 1.6em;
  line-height: 20px;
}
.sch_more_close {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 5px 15px;
  border: 0;
  border-radius: 30px;
  cursor: pointer;
  color: #2aba8a;
  background: transparent;
  border: 1px solid #2aba8a;
  text-align: center;
  vertical-align: top;
}

#tnb {
  position: relative;
  display: inline-block;
  float: right;
}
#tnb:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
#tnb .profile_btn {
  display: inline-block;
  border: 0;
  height: 30px;
  width: 30px;
  background: transparent;
  margin-top: 5px;
}
#tnb .profile_btn .profile_img img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
#tnb .profile_btn i {
  display: inline-block;
  font-size: 1.2em;
  padding: 0 5px;
  vertical-align: middle;
  color: #c1c4c6;
}
#tnb .tnb_login {
  display: inline-block;
  padding: 0 15px;
  margin-top: 5px;
  line-height: 30px;
  border: 1px solid #2aba8a;
  color: #2aba8a;
  border-radius: 30px;
}

.profile_img img {
  display: inline-block;
}

#tnb .tnb_member {
  display: none;
  position: absolute;
  top: 58px;
  right: 0;
  border-radius: 3px;
  width: 150px;
  background: #fff;
  z-index: 1;
  box-shadow: 0px 1px 5px 1px #e4e4e4;
}

/* 사이드 왼쪽 메뉴 */
#sidedrawer {
  position: fixed;
  top: 60px;
  bottom: 0;
  width: 240px;
  left: -240px;
  background: #fff;
  border-right: 1px solid #d9dfe8;
  overflow-x: hidden;
  overflow: auto;
  transition: transform 0.2s;
}
#sidedrawer.active {
  box-shadow: 0px 5px 10px 1px rgba(0, 0, 0, 0.26);
}
#sidedrawer:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}

#gnb {
  border-bottom: 1px solid #e6eaef;
}
#gnb h2 {
  position: absolute;
  font-size: 0;
  text-indent: -9999em;
  line-height: 0;
  overflow: hidden;
}
#gnb .gnb_1dul {
  font-size: 1.2em;
  font-weight: bold;
}
#gnb .gnb_1dli {
  width: 100%;
  position: relative;
  padding: 0;
}
#gnb .gnb_1da_on:before {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  border-left: 3px solid #ec5b5b;
}
#gnb .gnb_1da_on .gnb_2da:before {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  border-left: 3px solid #ec5b5b;
}

#gnb .gnb_1dli button {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 30px;
  height: 40px;
  overflow: hidden;
  border: 0;
  background: url("./img/mobile/gnb_bg2.png") no-repeat center;
  text-indent: -9999px;
}
#gnb .gnb_1dli button.btn_gnb_cl {
  background: url("./img/mobile/gnb_bg.png") no-repeat center;
}
#gnb .gnb_1da {
  display: block;
  text-align: left;
  color: #1c1827;
  line-height: 45px;
  padding: 0 20px;
}
#gnb .gnb_1da:hover,
#gnb .gnb_1da:hover i {
  color: #ec5b5b;
}
#gnb .gnb_1da i {
  color: #585e65;
}
#gnb .gnb_1da_on .gnb_2da:hover:before {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  top: 0;
  width: 2px;
  height: 100%;
  border-left: 3px solid #ec5b5b;
}

#gnb .gnb_2dul {
  display: none;
}
#gnb .gnb_2da {
  position: relative;
  font-size: 0.85em;
  line-height: 28px;
  padding: 0 20px;
  padding-left: 40px;
  display: block;
}
#gnb .gnb_2dli:hover .gnb_2da {
}
#gnb .gnb_2dli {
}

#gnb_close {
  position: absolute;
  top: 0;
  left: 260px;
  height: 30px;
  width: 30px;
  margin: 0;
  font-size: 22px;
  border: 0;
  background: transparent;
  color: #fff;
  overflow: hidden;
}

#gnb_empty {
  padding: 20px 0;
  color: #fff;
  text-align: center;
  line-height: 2em;
}
#gnb_empty a {
  display: inline;
  text-decoration: underline;
}

/* 메인, 공통 레이아웃 */
#wrapper {
  min-height: 100%;
  clear: both;
}
#wrapper:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}

#container {
  position: relative;
  padding: 20px;
  max-width: 1658px;
  margin: 61px auto 0;
}
#container:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
#container_title {
  font-size: 1.6em;
  font-weight: bold;
  padding: 20px;
  line-height: 30px;
  color: #333;
  text-align: left;
  background: #fff;
  border: 1px solid #e4e5e7;
  border-bottom: 0;
}
#container .conle {
  width: 75%;
  float: left;
}
#container .conri {
  width: 25%;
  float: left;
  margin-left: -1px;
  border: 1px solid #e4e5e7;
  border-bottom: 0;
}

.conle_idx_top {
}
.conle_idx_top:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
.conle_lt {
  position: relative;
  float: left;
  width: 50%;
  padding: 20px 30px;
  border: 1px solid #e4e5e7;
  background: #fff;
  margin-bottom: 1em;
}

.conle_lt:nth-child(odd) {
  clear: both;
}

.lt_gall {
  width: 100%;
}

.shortcut {
  padding: 10px 0;
}
.shortcut:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
.shortcut > li {
  display: block;
  font-size: 1.2em;
  font-weight: bold;
  background: #fff;
}
.shortcut > li > a {
  display: block;
  padding: 15px 20px;
