}

/* Mobile gnb */
#gnb .swiper-slide {
  width: auto;
}

/* PC gnb */
#gnb_all {
  display: none;
  position: absolute;
  width: 100%;
  z-index: 1;
}
#gnb_all .gnb_al_ul:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
#gnb_all .gnb_al_ul {
  background: #fff;
  border: 1px solid #1cc48f;
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
#gnb_all .gnb_al_li {
  float: left;
  width: 16.6%;
  min-height: 200px;
  padding: 5px;
  background: #fff;
  border-bottom: 1px solid #f6f7f9;
}
#gnb_all .gnb_al_li:nth-child(6n) {
  width: 17%;
  border-right: 0;
}
#gnb_all .gnb_al_li:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
#gnb_all .gnb_al_li .gnb_al_a {
  font-size: 1.2em;
  padding: 10px;
  display: block;
  position: relative;
  margin-bottom: 10px;
  font-weight: bold;
  color: #3f4156;
}
#gnb_all .gnb_al_li li {
  padding-left: 10px;
  line-height: 2em;
}
#gnb_all .gnb_al_li li i {
  color: #9ca6cc;
}
#gnb_all .gnb_al_li li a {
  color: #555;
}

.gnb_close_btn {
  position: absolute;
  top: -50px;
  left: 0px;
  width: 50px;
  height: 51px;
  background: #fff;
  color: #0b0b0b;
  vertical-align: top;
  font-size: 22px;
  border: 1px solid #1cc48f;
  border-bottom: 0;
}

/* 텍스트 크기 조절 */
#text_size {
  display: block;
  margin: 0 10px 20px;
  padding: 10px 20px;
  background: #1cc48f;
  text-align: center;
}
#text_size h2 {
  width: 0;
  height: 0;
  font-size: 0;
  line-height: 0;
  overflow: hidden;
}
#text_size button {
  display: inline-block;
  height: 35px;
  width: 35px;
  margin: 0 2px;
  text-indent: -9999px;
  border-radius: 30px;
  border: 0;
}

#text_size #size_down {
  background: url("./img/fct_small.png") #59d0aa center no-repeat;
  background-size: auto 20px;
}
#text_size #size_def {
  background: url("./img/fct_basic.png") #59d0aa center no-repeat;
  background-size: auto 20px;
}
#text_size #size_up {
  background: url("./img/fct_large.png") #59d0aa center no-repeat;
  background-size: auto 20px;
}

#text_size #size_down.ft_status,
#text_size #size_def.ft_status,
#text_size #size_up.ft_status {
  background-color: #fff;
}

.ts_down2 {
  font-size: 0.67em !important;
}
.ts_down1 {
  font-size: 0.83em !important;
}
.ts_def {
  font-size: 1em !important;
}
.ts_up1 {
  font-size: 1.167em !important;
}
.ts_up2 {
  font-size: 1.3em !important;
}

/* 중간 레이아웃 */
#m_side_gnb {
  display: none;
}
#content-wrapper {
  min-height: 100%;
  overflow-x: hidden;
  margin-left: 0px;
  transition: margin-left 0.2s;
  box-sizing: border-box;
  margin-bottom: -160px;
  padding-bottom: 160px;
}

#footer {
  box-sizing: border-box;
}

@media (min-width: 768px) {
  #header {
    left: 0;
  }
  #sidedrawer {
    transform: translate(240px);
  }
  #content-wrapper {
    margin-left: 240px;
  }
  body.hide-sidedrawer #header {
    left: 0;
  }
  body.hide-sidedrawer #sidedrawer {
    transform: translate(0px);
  }
  body.hide-sidedrawer #content-wrapper {
    margin-left: 0;
  }
  body.hide-sidedrawer #footer {
    margin-left: 0;
  }
}

#sidedrawer.active {
  transform: translate(240px);
}

#right_side {
  display: none;
}
#right_side:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
#right_side.active {
  padding-right: -200px;
}
#right_side.active #sidebar-wrapper {
  right: 0;
}

#right_side_inner {
  transition: all 0.4s ease 0s;
  height: 100%;
}
#right_side_inner:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}

#sidebar-wrapper {
  position: fixed;
  top: 60px;
  right: -200px;
  width: 260px;
  height: 100%;
  z-index: 1;
  transition: all 0.4s ease 0s;
}
#shorcut {
  float: left;
  width: 60px;
  height: 100%;
  border-left: 1px solid #e0e0e0;
  background: #fff;
}
#shorcut .sidebar_btn {
  display: block;
  text-decoration: none;
  width: 58px;
  margin-bottom: 60px;
  padding: 20px 10px;
  font-size: 1.6em;
  background: #fff;
  border: 0;
  -webkit-transition: background 0.5s;
  -moz-transition: background 0.5s;
  -o-transition: background 0.5s;
  -ms-transition: background 0.5s;
  transition: background 0.5s;
  color: #7f7f83;
}

#sidebar {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
}
#sidebar li {
  position: relative;
  padding: 0 10px;
  line-height: 40px;
  text-align: center;
}
#sidebar li a {
  display: block;
  text-decoration: none;
  color: #8facac;
  font-size: 0.97em;
}
#sidebar li a:active,
#sidebar li a:focus {
  text-decoration: none;
}
#sidebar li i {
  display: block;
  width: 35px;
  height: 35px;
  background: #f6f6e9;
  border-radius: 50%;
  color: #4fbeb5;
  text-align: center;
  line-height: 35px;
  font-size: 1.4em;
}
#sidebar li .arm_on,
#sidebar li .visit_num {
  position: absolute;
  top: 30px;
  right: 15px;
  text-align: right;
  background: #ee8580;
  display: inline-block;
  color: #fff;
  line-height: 10px;
  padding: 2px 5px;
  font-size: 0.8em;
  border-radius: 30px;
}

#side_tab {
  float: left;
  width: 200px;
  height: 100%;
  background: #f1f5f9;
}

/* 모바일 검색창 팝업 */
#m_sch,
#tnb .sch_more {
  display: none;
}

.top {
  position: relative;
}
.btn_top li {
  display: inline-block;
}
.btn_top a,
.btn_top .btn_submit {
}
.btn_top .btn_cancel/*, .btn_top .btn_b01*/ {
