/* 인풋 */
.chk_box {
  position: relative;
}
.chk_box input[type="radio"] {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  outline: 0;
  z-index: -1;
  overflow: hidden;
}
.chk_box input[type="radio"] + label {
  position: relative;
  padding-left: 23px;
  display: inline-block;
  color: #676e70;
}
.chk_box input[type="radio"] + label span {
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  display: block;
  background: #f7f8f9;
  border: 1px solid #cdd6df;
  border-radius: 50%;
}
.chk_box input[type="radio"]:checked + label {
  color: #3a8afd;
}
.chk_box input[type="radio"]:checked + label span {
  border-color: #3a8afd;
}
.chk_box input[type="radio"]:checked + label span:before {
  width: 7px;
  height: 7px;
  background: #3a8afd;
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  border-radius: 50%;
}
.chk_box input[type="checkbox"] + label {
  position: relative;
  color: #676e70;
}
.chk_box input[type="checkbox"] + label:hover {
  color: #2172f8;
}
.chk_box input[type="checkbox"] + label span {
  float: left;
  width: 15px;
  height: 15px;
  display: block;
  background: #fff;
  border: 1px solid #d0d4df;
  border-radius: 3px;
}
.chk_box input[type="checkbox"]:checked + label {
  color: #000;
}
.chk_box input[type="checkbox"]:checked + label span {
  background: url(./img/chk.png) no-repeat 50% 50% #3a8afd;
  border-color: #1471f6;
  border-radius: 3px;
}
.selec_chk {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  outline: 0;
  z-index: -1;
  overflow: hidden;
}

/* 페이징 */
.pg_wrap {
  clear: both;
  margin: 20px 0;
  text-align: center;
  background: #fff;
  padding: 15px 20px;
  border: 1px solid #e4e5e7;
}
.pg_wrap:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
.pg {
  display: block;
  text-align: center;
}
.pg:after {
  display: block;
  visibility: hidden;
  clear: both;
  content: "";
}
.pg .pg_page,
.pg .pg_current {
  display: inline-block;
  min-width: 36px;
  height: 36px;
  line-height: 34px;
  border-radius: 50%;
  padding: 0 5px;
  font-size: 1.083em;
  vertical-align: middle;
}
.pg a {
  display: block;
  color: #555;
}
.pg a:hover {
  background: #f3f3f3;
}
.pg .pg_current {
  background: #2aba8a;
  color: #fff;
  font-weight: bold;
}
.pg .pg_start {
  color: transparent;
  font-size: 0px;
}
.pg .pg_start:before {
  content: "\f100" !important;
  font-size: 18px;
  line-height: 34px;
  text-align: center;
  color: #959595;
  width: 0;
  height: 0;
  background: transparent;
  font-family: fontAwesome;
  font-style: normal;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pg .pg_prev {
  margin-left: -11px;
  color: transparent;
  font-size: 0px;
}
.pg .pg_prev:before {
  content: "\f104" !important;
  font-size: 18px;
